<template>
  <div class="errorCode">
    <toast :describe="errorMsg" v-if="ToastType==='publicToast'" position="bottom"></toast>
     <toast describe="Email or Password error, please try again." v-if="ToastType==='passwordError'" position="bottom"></toast>
    <toast :describe="email + ' is not registered, please register and log in!'"  v-if="ToastType==='notRegister'" position="bottom"></toast>
    <toast :describe="email + 'is not activated yet, please activate and  log in!'" v-if="ToastType==='notActive'" position="bottom"></toast>
    <toast describe= "undefined"  v-if="ToastType==='emailLoginFailed'" position="bottom"></toast>
    <toast describe= "Email format error, please enter the correct Email !"  v-if="ToastType==='formatError'" ></toast>
    <toast :describe= "'The email:' + signEmail + ' has been registered, please login directly.'"  v-if="ToastType==='registeredError'" position="bottom"></toast>
    <toast describe= "We have sent a confirmation email, please check!"  v-if="ToastType==='success'" position="bottom"></toast>
    <toast describe="网络连接失败、服务器异常、请求超时" v-if="ToastType==='networkError'" position="bottom"></toast>
    <toast describe="undefined!" v-if="ToastType==='emailForgotFailed'" position="bottom"></toast>
    <toast :describe="forgotEmail + ' is  not registered,please sign up first !'" v-if="ToastType==='notRegistered'" position="bottom"></toast>
    <toast :describe="$t('userPage.enterPassword')" v-if="ToastType==='inputOldPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.samePasswordError')" v-if="ToastType==='sameNewPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.passwordInconsistentError')" v-if="ToastType==='newPsdDifferError'" position="bottom"></toast>
    <toast :describe="$t('userPage.newlPassword')" v-if="ToastType==='limitPsdError'" position="bottom"></toast>

    <toast :describe="$t('userPage.originalPasswordError')" v-if="ToastType==='wrongPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.failedChangePassword')" v-if="ToastType==='changePsdFailedError'" position="bottom"></toast>
    <toast :describe="$t('userPage.successfullyChanged')" v-if="ToastType==='changePsdSuccessError'" position="bottom"></toast>
    <toast describe="Please try again for operation timeout!" v-if="ToastType==='overtime'" ></toast>

    <toast describe="on cycle 不能为零" v-if="ToastType==='setonCycleTime'" ></toast>

    <toast describe="off cycle 不能为零" v-if="ToastType==='setoffCycleTime'" ></toast>

    <toast describe=" night on cycle 不能为零" v-if="ToastType==='setonCycleTime-night'" ></toast>

    <toast describe="night off cycle 不能为零" v-if="ToastType==='setoffCycleTime-night'" ></toast>

    <toast describe="dayLength 超过预设时间" v-if="ToastType==='setTime-moredayLength'" ></toast>

    <toast describe="nightLength 超过预设时间" v-if="ToastType==='setTime-morenightLength'" ></toast>

    <toast describe="dayLength 不能为零 " v-if="ToastType==='setTime-dayLength'" ></toast>
    <toast describe=" 总时长范围只能设置为开始时间到24点之间 " v-if="ToastType==='setTime-dayLenthMore'" ></toast>
    <toast describe=" day循环时间范围不能大于day总时长 " v-if="ToastType==='setTime-DaycycleTimesMore'" ></toast>
    <toast describe=" night循环时间范围不能大于night总时长 " v-if="ToastType==='setTime-NightcycleTimesMore'" ></toast>
    <toast describe=" 白天加夜晚时长不能大于24小时 " v-if="ToastType==='setTime-daynightLengthMore'" ></toast>
    <toast describe=" 白天开始时间不能在晚上开始时间之后 " v-if="ToastType==='setTime-nightMoreday'" ></toast>
    <toast describe=" 白天时长超过晚上开始时间 " v-if="ToastType==='setTime-dayLengthMorenightS'" ></toast>
    <toast describe=" 晚上时长超过00:00 " v-if="ToastType==='setTime-nightLengthMoreZero'" ></toast>

    <toast describe="nightLength 不能为零" v-if="ToastType==='setTime-nightLength'" ></toast>

    <toast describe="day Start time 不能为零" v-if="ToastType==='setTime-dayStart'" ></toast>

    <toast describe="night Start time 不能为零" v-if="ToastType==='setTime-nightStart'" ></toast>

    <toast describe="day night 必须至少设置一种模式" v-if="ToastType==='setTime-moreThan'" ></toast>

    <toast describe="day Start 超过预设时间" v-if="ToastType==='setTime-front'" ></toast>
    <toast describe="开始时间只能在当前时间之后" v-if="ToastType==='setTime-LessCurrTime'" ></toast>

    <toast describe="night Start 超过预设时间" v-if="ToastType==='setTime-later'" ></toast>
    <toast describe="循环间隔 超过总时间" v-if="ToastType==='setTime-later'" ></toast>
    <toast describe="至少设置一种模式 day or night" v-if="ToastType==='onemoreTimingMode'" ></toast>

    <toast describe="你还没设置过任务呢，请先去设置任务再来尝试!" v-if="ToastType==='Nosetting'" ></toast>
    <toast describe="当前任务正在运行中，请关闭任务再操作!" v-if="ToastType==='disabledrturnon'" ></toast>

    <toast describe="请关闭普通定时模式再尝试哦" v-if="ToastType==='turnoffeaseyTiming'" ></toast>
    <toast describe="请关闭高级定时模式再尝试哦" v-if="ToastType==='turnoffseniorTiming'" ></toast>
    <toast describe="请关闭高级定时制热模式再尝试哦" v-if="ToastType==='turnoffseniorTimingHot'" ></toast>
    <toast describe="请关闭高级定时制冷模式再尝试哦" v-if="ToastType==='turnoffseniorTimingCode'" ></toast>
    <toast describe="请关闭普通制冷模式再尝试哦" v-if="ToastType==='turnoffCode'" ></toast>
    <toast describe="请关闭普通制热模式再尝试哦" v-if="ToastType==='turnoffHot'" ></toast>
    <toast describe="请关闭加湿模式再尝试哦" v-if="ToastType==='turnoffaddHumidity'" ></toast>
    <toast describe="请关闭减湿模式再尝试哦" v-if="ToastType==='turnoffreduceHUmidity'" ></toast>

    <toast describe="目标值不能超过当前湿度值" v-if="ToastType==='Humiditythencurrent'" ></toast>
    <toast describe="目标值不能小于当前湿度值" v-if="ToastType==='Humiditylesscurrent'" ></toast>
    <toast describe="目标值不能超过当前温度值" v-if="ToastType==='Temperaturethencurrent'" ></toast>
    <toast describe="目标值不能小于当前温度值" v-if="ToastType==='Temperaturelesscurrent'" ></toast>

    <toast describe="当前房间名已存在，请重新输入！" v-if="ToastType==='sameRoom'" ></toast>
    <toast describe="当前设备名称已存在，请重新输入！" v-if="ToastType==='sameDeviceName'" ></toast>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import toast from '../../components/common/toastError'
export default {
  name: 'errorCode',
  components: {
    toast
  },
  data () {
    return {}
  },
  computed: {
    ...mapState(['ToastType']),
    ...mapGetters(['errorMsg'])
  },
  watch: {
    ToastType (newVal) {
      if (newVal && newVal !== 'lowbattery') {
        console.log(newVal)
        setTimeout(() => {
          this.$store.state.ToastType = null
        }, 3000)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.errorCode{}
</style>



