/**
 * 查询系统语言返回 Language
 * @method systemLanguageResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    language    当前语言
 */

export const systemLanguageResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.systemLanguageResponse = function (language) {
        console.log('systemLanguageResponse:' + language)
        self.$i18n.locale = language
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 设置系统语言返回 Language
 * @method setSystemLanguageResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    language    当前语言
 * @param  {boolean}   result      是否设置成功
 */

export const setSystemLanguageResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.setSystemLanguageResponse = function (result, language) {
        if (result) {
          self.$i18n.locale = language
          // let storage = window.localStorage
          // storage.removeItem('timeData')
          // commonPatternListDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
          self.$router.go(-1)
        } else {
          self.$store.state.ToastType = 'setError'
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

