/**
 * 手机物理按键后退操作 Router
 * @method goBackResponse
 * @param  {string}   navigator   设备类型，必选
 * @param  {object}   self        this对象，必选
 */

export const goBackResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.goBackResponse = function () {
        // 主界面退出操作
        if (self.$route.name === 'me' || self.$route.name === 'login') {
          window.Router.goBackRequest()
          return false
        }
        self.$router.go(-1)
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

