/**
 * 定时列表数据返回 commonPatternListDataResponse
 * @method alarmHumidityValueResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {string}   data         定时列表数据返回
 */
export const commonPatternListDataResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.commonPatternListDataResponse = function (mac, ordinaryData, advancedData) {
        let data = JSON.parse(advancedData)
        try {
          let deviceList = self.deviceList
          deviceList.forEach(element => {
            if (element.mac === mac) {
              data.forEach(item => {
                if (item.state) {
                  element.timingId = item.id
                  element.mode = 'lightIMG'
                  element.run = item.state
                }
              })
            }
          })
          if (!self.devicesData) return false
          self.devicesData.forEach(element => {
            if (element.mac === mac) {
              if (data.length > 0) {
                data.forEach((item) => {
                  if (item.state) {
                    element.run = true
                    element.mode = 'lightIMG'
                    if (item.id === 1) {
                      element.lastMode = '001'
                    } else if (item.id === 2) {
                      element.lastMode = '010'
                    } else if (item.id === 2) {
                      element.lastMode = '010'
                    }
                    return item
                  }
                })
              }
              element.timingTask = data
            }
          })
          let locaDevices = JSON.parse(getStore('devicesData'))
          let doLocal = false
          locaDevices.forEach(ele => {
            if (ele.mac === mac) {
              if (data.length > 0) {
                data.forEach((item) => {
                  if (item.state) {
                    doLocal = true
                    ele.run = true
                    ele.mode = 'lightIMG'
                    if (item.id === 1) {
                      ele.lastMode = '001'
                    } else if (item.id === 2) {
                      ele.lastMode = '010'
                    } else if (item.id === 2) {
                      ele.lastMode = '010'
                    }
                  }
                })
                ele.timingTask = data
              }
            }
          })
          if (doLocal) {
            setStore('devicesData', locaDevices)
          }
        } catch (err) {
          console.log(err)
        }
        console.log(self)
        if (self.currentDeviceMAC === mac && self.deviceControlData) {
          let timestr, offTime
          if (data.length === 0) {
            return false
          }
          timestr = data[0].onCycle.split(':')
          offTime = data[0].offCycle.split(':')
          data.forEach((ele, index) => {
            if (ele.state) {
              switch (ele.id) {
                case 1:
                  timestr = data[index].onCycle.split(':')
                  offTime = data[index].offCycle.split(':')
                  break
                case 2:
                  timestr = data[index].onCycle.split(':')
                  offTime = data[index].offCycle.split(':')
                  break
                default:
                  break
              }
            }
          })
          console.log('index页面 onoff')
          console.log(timestr)
          console.log(offTime)
          setStore('Timing', data)
          timestr[0] = parseInt(timestr[0]) < 10 ? '0' + parseInt(timestr[0]) : timestr[0]
          timestr[1] = parseInt(timestr[1]) < 10 ? '0' + parseInt(timestr[1]) : timestr[1]
          offTime[0] = parseInt(offTime[0]) < 10 ? '0' + parseInt(offTime[0]) : offTime[0]
          offTime[1] = parseInt(offTime[1]) < 10 ? '0' + parseInt(offTime[1]) : offTime[1]
          self.deviceControlData.onTime = timestr[0] + ':' + timestr[1]
          self.deviceControlData.offTime = offTime[0] + ':' + offTime[1]
          self.deviceControlData.switch = data[0].state
        }
        if (page === 'room') {
          self.deviceData.forEach(element => {
            if (element.mac === mac) {
              data.forEach(item => {
                if (item.state) {
                  element.timingId = item.id
                  element.mode = 'lightIMG'
                  element.run = item.state
                }
              })
            }
          })
        } else if (page === 'timerMode' && self.currentDeviceMAC === mac) {
          console.log('定时页面逻辑')
          let dayRun, nightRun
          if (!data) {
            self.$store.state.user.timingRun = true
            self.$store.state.user.StimingRun = true
            return false
          }
          data.forEach((element) => {
            switch (element.id) {
              case 1:
                self.onCycleTime = element.onCycle.split(':')
                self.offCycleTime = element.offCycle.split(':')
                self.$store.state.user.timingRun = !element.state
                self.pickerScroll(1, self.onCycleTime)
                break
              case 2:
                self.DonCycleTime = element.onCycle.split(':')
                self.DoffCycleTime = element.offCycle.split(':')
                self.dayStart = element.time.split(':')
                // self.dayLength =
                self.dayLength = getTimeLength(element.time.split(':'), element.time2.split(':')).split(':')
                self.pickerScroll(2, self.dayLength)
                dayRun = element.state
                self.daySwitch = element.state
                if (element.state) {
                  self.currentMode = 'dayLength'
                  self.timeMode = 'day'
                }
                break
              case 3:
                self.nightONCycle = element.onCycle.split(':')
                self.nightOffCycle = element.offCycle.split(':')
                self.nightStart = element.time.split(':')
                self.nightLength = getTimeLength(element.time.split(':'), element.time2.split(':')).split(':')
                if (element.state) {
                  self.pickerScroll(2, self.nightLength)
                  self.buttonType = 'nightLength'
                  self.timeMode = 'night'
                }
                console.log(self.nightLength)
                nightRun = element.state
                self.nightSwitch = element.state
                break
              default:
                break
            }
          })
          if (dayRun || nightRun) {
            self.$store.state.user.StimingRun = false
            // self.dayNightTab = false
          } else {
            self.$store.state.user.StimingRun = true
          }
          console.log(self.StimingRun)
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

