export const switchNetworkResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log('IOS')
      window.switchNetworkResponse = function (type, state) {
        if (self.$route.path === '/Me/configwifi') {
          if (type === 'WIFI') {
            // 获取wifi名称
            WiFiNameRequest(self.NAVIGATOR, self)
          }
        } else if (self.currentRouter === 'connectRoute' || self.currentRouter === 'configwifi') {
          if (type === 'None') {
          // self.$store.state.ToastType = 'networkSwitch'
            self.$router.push({path: '/Me/configureFailed'})
            stopConnectionNetworkRequest(self.NAVIGATOR, self)
          }
        }
      }
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

/**
 * WiFi插座
 * 配置WiFi网络返回 Add
 * @method  WiFiConfigResponse
 * @param   {string}    navigator   设备类型，必选
 * @param   {boolean}   result      配置是否成功，必选
 */
export const WiFiConfigResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log('IOS')
      window.WiFiConfigResponse = function (result) {
        if (result) {
          let localWifiInfo = JSON.parse(getStore('localWifiInfo'))
          if (localWifiInfo) {
            let flag = localWifiInfo.find(item => {
              return item.name === self.WIFIValue
            })
            if (!flag) {
              let obj = {
                name: self.WIFIValue,
                password: self.WIFIPassword
              }
              localWifiInfo.push(obj)
              setStore('localWifiInfo', localWifiInfo)
            } else {
              // 刷新本地密码
              localWifiInfo.forEach((ele) => {
                if (ele.name === self.WIFIValue) {
                  ele.password = self.WIFIPassword
                }
              })
              setStore('localWifiInfo', localWifiInfo)
            }
          } else {
            localWifiInfo = [
              {
                name: self.WIFIValue,
                password: self.WIFIPassword
              }
            ]
            setStore('localWifiInfo', localWifiInfo)
          }
          self.$router.push({
            path: '/Me/connectRoute'
          })
        } else {
          self.$router.push({path: '/Me/Noconnectwifi'})
        }
      }
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

