/**
 * WiFi插座
 * 解绑设备返回  DeviceList
 * @method  unbundlingDeviceResponse
 * @param   {string}    navigator   设备类型，必选
 * @param   {boolean}   result      是否解绑成功，必选
 */

export const unbundlingDeviceResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log('IOS')
      window.unbundlingDeviceResponse = function (result, mac) {
        self.animFlag = false
        clearTimeout(self.timer)
        console.log(self)
        if (result) {
          console.log('匹配成功')
          let id = null
          let roomData = JSON.parse(getStore('roomData'))
          roomData.forEach((ele) => {
            if (ele.mac.indexOf(mac) !== -1) {
              id = ele.id
            }
          })
          if (id !== null) {
            // let id // 房间id
            // roomData.forEach(element => {
            //   if (element.name === self.getRooomName) {
            //     id = element.id
            //   }
            // })
            console.log(mac)
            let macs = roomData[id].mac
            let icons = roomData[id].icon
            macs = macs.split('&').filter((item, index) => {
              if (item !== mac) {
                return item
              } else {
                console.log(icons)
                icons.splice(index, 1)
                console.log(icons)
              }
            })
            let macStr = ''
            macs.forEach(element => {
              macStr += '&' + element
            })
            console.log(macStr)
            roomData[id].mac = macStr.slice(1)
            roomData[id].icon = icons
            // Object.assign(roomData[id], {icon: icons})
            console.log(JSON.stringify(roomData))
            setStore('roomData', JSON.stringify(roomData))
            console.log(roomData)
            // roomData = Object.assign(roomData, roomData)
            self.setRoomdata(roomData)
            console.log(self.roomList)
            // 删除房间列表图标
            // let roomList = self.roomList
            // console.log(roomList)
            // roomList.forEach(element => {
            //   let index = element.mac.split('&').indexOf(mac)
            //   console.log(element.mac, element.mac.split('&'), index)
            //   if (index !== -1) {
            //     element.icon.splice(index, 1)
            //   }
            // })
          }
          self.removeSuccess = true
          self.deviceList.forEach((val, i, arr) => {
            console.log(val.mac)
            console.log('设备mac')
            console.log(mac)
            if (val.mac === mac) {
              arr.splice(i, 1)
              console.log('解绑设备匹配成功')
            }
          })
          let deviceData = JSON.parse(getStore('devicesData'))
          deviceData.forEach((val, i, arr) => {
            if (val.mac === mac) {
              arr.splice(i, 1)
            }
          })
          setStore('devicesData', deviceData)
          // let localDevicedata = JSON.parse(getStore('devicesData'))
          let devicesData = self.devicesData
          devicesData.forEach((val, i, arr) => {
            if (val.mac === mac) {
              arr.splice(i, 1)
            }
          })
          // setStore('devicesData', localDevicedata)
          self.setDevicesdata(devicesData)
          self.setDevicelist(self.deviceList)
          if (self.currentRouter !== 'Me') {
            self.$router.push({
              path: '/Me'
            })
          }
        } else {
          self.removeFailed = true
        }
        setTimeout(() => {
          self.removeSuccess = false
          self.removeFailed = false
          clearTimeout()
        }, 2000)
      }
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

